:root {
  --header-text-color: #ec7f37;
  --main-bg-color: #1d1e22;
  --main-text-color: #ffffff;
}

.header {
  display: flex;
  justify-content: space-around;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.project {
  display: flex;
  flex-direction: column;
  color: var(--main-text-color);
  padding: 5%;
}

.project-title {
  padding-bottom: 2%;
}

.header > div {
  padding: 10px;
  text-align: center;
  font-size: 2em;
  color: var(--header-text-color);
}

html,
body {
  background-color: var(--main-bg-color);
  margin: 10px;
}

.profileText {
  color: var(--main-text-color);
}

.project-modal {
  display: flex;
  justify-content: center;

  color: var(--main-text-color);
}

.modal-close {
  color: var(--main-text-color);
}
